import React from 'react';
import Layout from "../components/layout";
import SEO from "../components/seo";
import Category from "../components/category";
import './page.css';
import { Link } from "gatsby";

const fitImage = {
  width: "100%",
  height: "100%"
}

const ProList = (props) => {
  return (
    <div>
      <h2>
        <i className="fas fa-users blue"></i>  Client
      </h2>
      <h3>{props.client}</h3>
      <h2>
        <i className="far fa-clipboard blue"></i>  Project
      </h2>
      <p>{props.projectDiscription}</p>
    </div>
  )
}

const PageTemplate = props => {

  const mainImage = props.pathContext.mainImage;
  const srcOne = props.pathContext.proOne.image;
  const proOneDis = props.pathContext.proOne.description;
  const client = props.pathContext.proOne.client;
  const title = props.pathContext.title;

  return(
    <Layout>
      <SEO title="Home" />
      <div className="projectContainer">
        <div id="proHeader" className={mainImage} >
          <h1>{title}</h1>
          <Link to="/" className="scroll-down">
            <i className="fas fa-arrow-down"></i>
          </Link>
        </div>

        <div className="cardsContainer">
          <div className="cards">
            <img src={srcOne}  style={fitImage} alt="Project images"/>
          </div>
          <div className="cards description" >
            <ProList projectDiscription={proOneDis} client={client}/>
          </div>
        </div>
        <div className="cardsContainer">
          <div className="cards description">
            <ProList projectDiscription={proOneDis} client={client}/>
          </div>
          <div className="cards">
            <img src={srcOne}  style={fitImage} alt="Project images"/>
          </div>
        </div>

        <div>
          <Category divClass={"categoryLayout"}/>
        </div>
      </div>
    </Layout>
  )
};

export default PageTemplate;